<template>
	<view>
		<view class="myhead">
			<view class="myinfo">
				<view class="myinfoleft">
				<image :src="user.headurl"></image>
				</view>
				<view class="myinforight">
					<view>{{user.name}}</view>
					<view>{{user.unit}}</view>
				</view>
			</view>
			<view class="mysanlie">
				<view class="mypaiming">
					<view>{{user.paiming}}</view>
					<view>排名</view>
				</view>
				<view class="mypaiming">
					<view>{{user.jifen}}</view>
					<view>积分</view>
				</view>
				<view class="mypaiming ">
					<view>{{user.liulan}}</view>
					<view>浏览</view>
				</view>
			</view>
		</view>
		<view class="itemlist">
			<view class="listitem" v-for="(item,index) in funct" :key="index" @tap="tourl(item.tourl,item.kefu)">
				<view class="itemleft">
					<image :src="item.iconurl"></image>
					<view>{{item.title}}</view>
				</view>
				<view class="itemright">
					<view class="kefu" v-if="item.kefu">{{item.kefu}}</view>
					<view class="righticon"></view>
				</view>
			</view>
		</view>
		<view class="huoqu" v-if="xian">
			<image src="../../static/image/wxgficon.png" mode=""></image>
			<view class="btnbor"></view>
			<view class="shao">会获取您的昵称、头像、手机号、地区及性别</view>
			<view style="display: flex;justify-content: space-between;">
			<button class="fan" @tap="fan()">返回</button>
			<button class="deng" open-type="getPhoneNumber" @getphonenumber="getPhoneNumber" @click="getUserInfo">授权登录</button>
			</view>
		</view>
	</view>
</template>

<script>
	export default{
		data(){
			return{
				xian:false,
				user:{
					// name:uni.getStorageSync('userInfo').nickname,
					// unit:uni.getStorageSync('userInfo').orgName,
					// headurl:uni.getStorageSync('userInfo').avater,
					// paiming:uni.getStorageSync('userInfo').rank,
					// jifen:uni.getStorageSync('userInfo').wallet,
					// liulan:uni.getStorageSync('userInfo').view,
					name:'',
					unit:'',
					headurl:'',
					paiming:'',
					jifen:'',
					liulan:'',
					xian:false,
					user_id:''
				},
				funct:[
					{
						iconurl:'data:image/png;base64,',
						title:'提交记录',
						tourl:'./tijiaojilu'
					},{
						iconurl:'data:image/png;base64,',
						title:'我的入驻',
						tourl:'./ruzhudet'
					},{
						iconurl:'data:image/png;base64,',
						title:'联系我们',
						kefu:''
					},{
						iconurl:'data:image/png;base64,',
						title:'隐私协议',
						tourl:'./yinsi'
					},{
						iconurl:'data:image/png;base64,',
						title:'用户协议',
						tourl:'./xieyi'
					}
				]
			}
		},
		onShareAppMessage() {},
		onLoad() {
			let that = this
			uni.request({
				url:'https://jiafeng.boyaokj.cn/api/index/setting',
				method:'GET',
				data:{
					key:'kefu'
				},
				success(res) {
					that.funct[2].kefu = res.data.data.data 
				}
			})
			
		},
		onShow() {
			let that = this
			uni.login({
				provider: 'weixin',
				  success: function (res) {
				    // console.log(res);
						uni.request({
							url:'https://jiafeng.boyaokj.cn/api/wechat/login',
							method:'GET',
							data:{
								code:res.code
							},
							success(res) {
								// console.log(res)
								that.user_id = res.data.data.user_id
							}
						})
				  }
			})
			if(uni.getStorageSync('userInfo').user_id == null){
				this.xian = !this.xian
			}
			uni.request({
				url:'https://jiafeng.boyaokj.cn/api/wechat/getUserinfo',
				method:'GET',
				data:{
					user_id:uni.getStorageSync('userInfo').user_id
				},
				success(red) {
					console.log(red.data.data.nickname)
					that.user.name=red.data.data.nickname,
					that.user.unit=red.data.data.orgName,
					that.user.headurl=red.data.data.avater,
					that.user.paiming=red.data.data.rank,
					that.user.jifen=red.data.data.wallet,
					that.user.liulan=red.data.data.view,
					uni.setStorage({
						key:'userInfo',
						data:red.data.data,
					})
				}
			})
			
		},
		onHide(){
			if(uni.getStorageSync('userInfo').user_id == null){
				this.xian = !this.xian
			}
			
		},
		methods:{
			fan(){
				uni.switchTab({
					url:'../index/index'
				})
			},
			tourl(url,kefu){
				uni.navigateTo({
					url:url
				})
				uni.makePhoneCall({
					 phoneNumber: kefu, 
				})
		},
		getUserInfo(){
			let that = this
			 uni.getUserProfile({
					 desc:'Wexin',     // 这个参数是必须的
					 success:res=>{
							 // console.log(res)
							 let data = JSON.parse(res.rawData)
							 uni.request({
							 	url:'https://jiafeng.boyaokj.cn/api/wechat/setUserinfo',
								method:'GET',
								data:{
									nickname:data.nickName,
									avater:data.avatarUrl,
									country:data.country,
									gender:data.gender,
									province:data.province,
									city:data.city,
									user_id:that.user_id,
								},
								success(red) {
									// console.log(red.data.data)
									uni.setStorage({
										key:'userInfo',
										data:red.data.data
									})
								}
							 })
					 }
			 })
			},
			getPhoneNumber(e) {
							 let that = this 
								uni.login({
									provider: 'weixin',
									success(res) {
										// console.log(res)
										uni.request({
											url:'https://jiafeng.boyaokj.cn/api/wechat/setMobile',
											method:'GET',
											data:{
												user_id:uni.getStorageSync('userInfo').user_id,
												code:res.code,
												iv:e.detail.iv,
												encrypteddata:e.detail.encryptedData
											},
											success(res) {
												that.xian = !that.xian
												// console.log(res)
												// uni.setStorage({
												// 	key:'userInfo',
												// 	data:res.data.data
												// })
												uni.request({
													url:'https://jiafeng.boyaokj.cn/api/wechat/getUserinfo',
													method:'GET',
													data:{
														user_id:uni.getStorageSync('userInfo').user_id
													},
													success(red) {
														// console.log(red.data.data)
														uni.setStorage({
															key:'userInfo',
															data:red.data.data,
														})
														uni.switchTab({
															url:'../index/index'
														})
													}
												})
											}
										})
									}
								})
			           },
		}
	}
</script>

<style>
	.huoqu{
		position: fixed;
		top: 0;
		background-color: #fff;
		/* height: 200rpx; */
		width: 100%;
		height: 100%;
		box-sizing: border-box;
		padding-top: 60%;
		z-index: 50;
		left: 0;
	}
	.huoqu image{
		width: 200rpx;
		height: 200rpx;
		margin: auto;
		display: block;
	}
	.shao{
		color: #333;
		text-align: center;
		font-size: 32rpx;
		margin-top: 80rpx;
		margin-bottom: 60rpx;
	}
	.btnbor{
		width: 680rpx;
		margin: 20rpx auto;
		height: 1rpx;
		background: #999;
		opacity: .5;
		margin-top: 80rpx;
	}
	.fan{
		background: #fff;
		color: #333;
		font: 32rpx;
		width: 290rpx;
		height: 80rpx;
		display: flex;
		margin: auto;
		justify-content: center;
		align-items: center;
		border: 3rpx solid #fff;
		border: 0!important;
		border-radius: 40rpx;
	}
	.deng{
		background: #67c23a;
		color: #fff;
		font: 32rpx;
		width: 290rpx;
		height: 80rpx;
		display: flex;
		margin: auto;
		justify-content: center;
		align-items: center;
		border: 1rpx solid #67c23a;
		border: 0!important;
		border-radius: 40rpx;
	}
	.kefu{
		font-size: 28rpx;
		font-family: PingFangSC-Regular, PingFang SC;
		font-weight: 400;
		color: #666666;
		margin-right: 20rpx;
	}
	.listitem{
		width: 720rpx;
		height: 101rpx;
		background: #FFFFFF;
		border-radius: 6rpx;
		display: flex;
		justify-content: space-between;
		margin: auto;
		margin-top: 16rpx;
		padding: 0 30rpx 0 22rpx;
		box-sizing: border-box;
	}
	.itemleft view{
		font-size: 30rpx;
		font-family: PingFangSC-Regular, PingFang SC;
		font-weight: 400;
		color: #1E2432;
		margin-left: 23rpx;
	}
	.itemleft,.itemright {
		display: flex;
		align-items: center;
	}
	.itemleft image{
		width: 48rpx;
		height: 48rpx;
	}
	.righticon{
		width: 14rpx;
		height: 14rpx;
		border-top: 5rpx solid #C2C4CA;
		border-right: 5rpx solid #C2C4CA;
		transform: rotate(45deg);
	}
	.mypaiming view:nth-child(1){
		font-size: 34rpx;
		font-family: Helvetica;
		color: #1E2432;
		text-align: center;
	}
	.mypaiming view:nth-child(2){
		font-size: 30rpx;
		font-family: PingFangSC-Regular, PingFang SC;
		font-weight: 400;
		color: #ACB1C0;
		text-align: center;
	}
	.mysanlie{
		width: 515rpx;
		margin: auto;
		display: flex;
		justify-content: space-between;
		padding-top: 58rpx;
		padding-bottom: 36rpx;
	}
	.myinforight{
		margin-left: 48rpx;
		margin-top: 56rpx;
	}
	.myinforight view:nth-child(1){
		font-size: 50rpx;
		font-family: PingFangSC-Regular, PingFang SC;
		font-weight: 400;
		color: #1E2432;
		margin-bottom: 6rpx;
	}
	.myinforight view:nth-child(2){
		font-size: 26rpx;
		font-family: PingFangSC-Regular, PingFang SC;
		font-weight: 400;
		color: #485164;
	}
	.myinfo{
		display: flex;
		margin-left: 44rpx;
	}
	.myinfoleft image{
		width: 200rpx;
		height: 200rpx;
		border: 10rpx solid #fff;
		border-radius: 150rpx;
	}
	.myhead{
		background-image: url(data:image/png;base64,);
		padding-top: 128rpx;
		background-size: 100%;
		background-repeat: no-repeat;
	}
</style>
